.card-container{
    display: flex;
    flex-wrap: wrap;
    margin-left: 15rem;
    margin-top: 2rem;
    z-index: -2;

}
.card{
    margin: 10px;
    border: 2px solid #ededed;
    padding: 10px;
    cursor: pointer;
}
.card-img{
    width: 10rem;
    margin-bottom: 1rem;
}
.card-title{
    margin-bottom: 1rem;
}
.card-reviews{
    margin-bottom: 1rem;
    display: flex;

}
.ratings-start{
    color: #d5ab55;
}
.total-reviews{
    font-size: 0.9rem;
    margin-left: 10px;
}
.card-price{
    display: flex;
    justify-content: space-around;
    align-items: center;
    
}
.bag-icon{
    color: #535353;
}